.app-list-item {
    font-size: 1.25rem; }

.app-list-item button {
    width: 35px;
    height: 35px;
    margin: 3px;
    font-size: 17px;
    border: none;
    cursor: pointer;
    background-color: #fff; }

.app-list-item button:focus {
    box-shadow: none;
    outline: none; }

.app-list-item .btn-star {
    color: #aeaeae; }

.app-list-item .btn-trash {
    color: red; }

.app-list-item .fa-heart {
    width: 35px;
    height: 35px;
    text-align: center;
    line-height: 35px;
    font-size: 16px;
    color: red;
    transition: 0.3s all;
    transform: translateX(30px);
    opacity: 0; }


.app-list-item-label {
    display: flex;
    align-items: center;
    margin-right: 15px;
    line-height: 1.5;
    cursor: pointer;
    user-select: none;
    transition: 0.5s all; }


.app-list-item.like .fa-heart {
    opacity: 1;
    transform: translateX(0px); }


.app-list-item.important .app-list-item-label {
    color: #FFD700; }


.app-list-item.important .btn-star {
    color: #FFD700; }

